import React, { useEffect, useState } from 'react'
import Amplify , { API , graphqlOperation } from 'aws-amplify'
import { createTodo } from './graphql/mutations'
import { listTodos } from './graphql/queries'
import awsExports from "./aws-exports";
import { Storage } from 'aws-amplify';
import ShowShowy from './components/showShowy'


Amplify.configure( awsExports )


const initialState = {  name : '' ,         email : '' , 
                        image : '' ,        fullname : '' ,
                        brand : 'webb' ,    status : 'filling' ,
                        whiteoverlay : false , coloredbg : false
}

const contents = {
    top01 : 'Upload Your Customer\'s Logo.' ,
    top02 : 'For a Custom Catalog!' ,
    top1 :  'For best results, upload a logo limited to three colors, withot a background.' ,
}




const Appi = ( ) => {
    const [ formState , setFormState ] =    useState( initialState )
    const [ todos , setTodos ] =            useState( [ ] )
    const [ msg , setMsg ] =                useState( 'enter file + name + email' )
    const [ showy , setShowy ] =            useState( 1 )
    const [ infile , setInfile ] =          useState( { } )
    const [ modalShow , setModalShow ] =    useState( false );

    // const [ final , setFinal ] =         useState( { } )
    // const [ finalKey , setFinalKey ] =   useState( '' )


    useEffect( ( ) => { fetchTodos( ) } , [ ] )

    

    function setInput( key , value ) { 
        setFormState( { ...formState , [ key ] : value } ) }
    


    async function fetchTodos( ) {
        try {
            const todoData = await API.graphql( graphqlOperation( listTodos ) )
            const todos = todoData.data.listTodos.items
            setTodos( todos )
        } catch ( err ) {
            console.log( 'error fetching todos' + err );
            console.log( err )
        }
    }
    

    async function setShowyBowie( n ) { setShowy( n ) }


    async function setFile( i ) { setInfile( i.target.files[ 0 ] ) }
    

    // async function getFinal( ) {
    //     // var getKey = finalKey;
    //     var getKey = 'FINALS/CUSTOM_CATALOG_PUEBLOLOCO.pdf'
    //     var finImage = await Storage.get( getKey );
    //     console.log( finImage )
    //     setFinal( )// }


    const messages = {
        logo :      'PLEASE ENTER NAME FOR LOGO' ,
        email :     'PLEASE ENTER EMAIL' ,
        fullname :  'PLEASE ENTER CONTACT INFO' ,
        image :     'PLEASE SELECT A FILE TO UPLOAD' ,
        brand :     'PLEASE ENTER DESIRED CATALOG BRAND'
    }


    async function addLogo( ) {
        try {
            var { name , email , image , fullname ,
                  brand , whiteoverlay , coloredbg } = formState

            if ( !name || !email || !image || !fullname || !brand ) {
                function cs( ms ) {
                    console.log( ms )
                    setMsg( ms )
                    return
                }
                if ( !name ) {
                    return cs( messages.logo )
                } else if ( !email ) {
                    return cs( messages.email )
                } else if ( !fullname ) {
                    return cs( messages.fullname )
                } else if ( !image ) {
                    return cs( messages.image )
                } else if ( !brand ) {
                    return cs( messages.brand )
                } else {
                    return
                }            
            }

            function mkValid( st ) {
                var inv = /[<>:"/\\|&'%?*]/g
                return st
                    .replace( inv , '-' )
                    .replace( /,/g , '-' )
                    .replace( /\s/g , '-' )
            }

            var regEmail = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/
            if ( !regEmail.test( formState.email ) ) {
                console.log( 'INVALID EMAIL' )
                setMsg( 'INVALID EMAIL' )
                return
            }


            setMsg( 'FINISHING...' )
            setInput( 'status' , 'submitting' )
            setModalShow( true )
            console.log( modalShow )

            const todo = { ...formState }
            var inFile = infile;
            setTodos( [ ...todos , todo ] )
            setFormState( initialState )
            setInfile( { } )


            var nm = ( name )           ? mkValid( name ) : '_xname';
            var em = ( email )          ? mkValid( email ) : '_xe@ma.il'
            var fl = ( fullname )       ? mkValid( fullname ) : '_xfullname'
            var br = ( brand )          ? mkValid( brand ) : '_xbrand'
            var wo = ( whiteoverlay )   ? 'wo' : 'no'
            var bg = ( coloredbg )      ? 'bg' : 'jg'
            var extension = ( image ) 
                ? formState.image.substring( image.lastIndexOf( '.' ) )
                : '_xext'
            var formatted = `[${nm},${em},${fl},${br},${wo},${bg}]${extension}`
            console.log( inFile )
            console.log( formatted )

            // setInput( 'submitting' , true )
            // showFinalOpts( )
            // setFinalKey( formatted )
            await API.graphql( graphqlOperation( createTodo , { input: todo } ) )
            var result = await Storage.put( formatted , inFile )

            setMsg( 'THANKS FOR YOUR SUBMISSION!' )
            console.log( "result" )
            console.log( result )
            // setShowyBowie( 2 )
        } catch ( err ) {
            console.log( 'Error Creating Todo:' , err )
        }
    }




    return (
        <div
            className="cont"
            style={ { overflow : 'hidden'} } 
            >

            <ShowShowy
                formState={ formState }
                msg={ msg }
                showy={ showy }
                setShowyBowie={ setShowyBowie }
                setInput={ setInput }
                addLogo={ addLogo }
                setFile={ setFile }
                contents={ contents }
                modalShow={ modalShow }
                setModalShow={ setModalShow }
                setMsg={ setMsg }
                />

         

        </div>
    )
}



export default Appi