import React from 'react'
import Form from 'react-bootstrap/Form'
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'


export default function MyVerticallyCenteredModal( props ) {
    return (
        <div>
        <Modal
            { ...props.show }
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
            >
            <Modal.Header closeButton>
                <Modal.Title id="contained-modal-title-vcenter">
                    Special Actions
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form>
                    <Form.Group controlId="formBasicCheckbox">
                        <Form.Check
                            onChange={ ( ) => { props.setInput( 'whiteoverlay' , !props.formState.whiteoverlay ) } }
                            checked={ props.formState.whiteoverlay }
                            type="checkbox" 
                            label='white overlay' />
                    </Form.Group>

                    <Form.Group controlId="formBasicCheckbox">
                        <Form.Check 
                            onChange={ ( ) => { props.setInput( 'coloredbg' , !props.formState.coloredbg ) } }
                            checked={ props.formState.coloredbg }                            type="checkbox"
                            label='other action' />
                    </Form.Group>
                </Form>
                <p>Select 'other action' if your logo is this or that or the other.</p>
            </Modal.Body>

            <Modal.Footer>
                <Button onClick={ props.onHide } >final submit</Button>
            </Modal.Footer>
        </Modal>
        </div>
    );
}
