import React from 'react'
import { Col, Container, Row } from 'react-bootstrap';
import Button from 'react-bootstrap/Button'
import spins from '../spins.svg'
import hpgblack from '../hpg_logo_black.png'
// import nineloading from '../nineloading.gif'

export default function SubmittedPage ( props ) {

        const setShowyBowie = props.setShowyBowie;

        return (

        <div  className="disp" id="clientCustom">

            <br />


            <Container 
                fluid
                 >

                <Row>
                    <Col
                        style={ { height : '80%' , marginTop : 'auto' , marginBottom : 'auto' } }>
                        <a href="https://hpgbrands.com">
                            <img src={ hpgblack } width="70%" alt="hpgblack" />
                        </a>
                    </Col>

                    <Col>
                       <img alt="pain" width="80%" src="data:img/png;base64,"/>
                    </Col>
                </Row>

                {/* <br/> */}

                <Row>
                    
                    <Col>
                        <h1 
                            className="empher topmartit"
                            style={ { fontSize : '42px' , textAlign : 'center' , color : '#f45968' } } >
                            THANKS FOR YOUR SUBMISSION
                        </h1>
                        {/* <br/> */}

                        <div 
                            className="instructions topmar" 
                            style={ { fontFamily : 'Montserrat' , fontWeight : 400 } } >
                            <p>Check Your Email for a Custom Catalog</p>
                            <br />
                        </div>
                    </Col>
                </Row>


                <Row style={ { height : '30vh' } } >

                    <Col>.</Col>
                    
                    <Col>
                        <img src={ spins } alt="spins" />
                    </Col>

                    <Col>.</Col>
                </Row>


                <Row>
                    <Col>
                        <Button
                            className="btn"
                            id="submitanotherlogobutton"
                            onClick={ ( )=>setShowyBowie( 1 ) } >
                            SUBMIT ANOTHER LOGO
                        </ Button>
                    </Col>
                </Row>

                {/* <br /> */}
                <br />

                <Row>
                        <p className="caveat instructions" >
                                Due to the wide variation of logos, if your final catalog needs any alterations, please reply to your email with instructions on how we can make this perfect and we will get you a new catalog within 24 hours.
                        </p>
                </Row>
 
            </Container>
        </div>
    )
}