import React from 'react'
import SubmittedPage from './submittedPage'
import Plodes from './plodesPage'
import MyVerticallyCenteredModal from './actionsModal'



export default function ShowShowy( props ) {
    var formState = props.formState;
    var showy = props.showy;
    var msg = props.msg;
    var setShowyBowie = props.setShowyBowie;
    var setInput = props.setInput;
    var addLogo = props.addLogo;
    var setFile = props.setFile;
    var contents = props.contents;
    var modalShow = props.modalShow;
    var setMsg = props.setMsg;
    var setModalShow = props.setModalShow;

    // var final = props.final;
    
    function handleTheDropDown( eventK ) {
        console.log( eventK )
        setInput( 'brand' , eventK )
    }
    // showy = 2

    if ( showy === 2 ) {
        return <SubmittedPage setShowyBowie={ setShowyBowie } />

    } else if ( showy === 3 ) {
        return(
        <div style={ {
                height : '300vw' ,
                width : '100vw' ,
                overflow : 'hidden' } } >
                    <p>asfd</p></div> )

    } else {
        return (
            <div>
            <Plodes
                setInput=           { setInput }
                formState =         { formState }
                showy =             { showy }
                msg =               { msg }
                setShowyBowie =     { setShowyBowie }
                addLogo =           { addLogo }
                setFile =           { setFile }
                contents =          { contents }
                handleTheDropDown = { handleTheDropDown }
                modalShow =         { modalShow }
                setMsg=             { setMsg }
                />

            <MyVerticallyCenteredModal
                formState={ formState }
                setInput={ setInput }
                    show={ modalShow }
                    onHide={ ( ) => {
                        setMsg( 'UPLOADING . . .')
                        setModalShow( false )
                        setShowyBowie( 2 )
                    } }
                />
                </div>
        ) 
    }
}