import Dropdown from 'react-bootstrap/Dropdown'
import React from 'react'
import Button from 'react-bootstrap/Button'
import hpgcust2 from '../hpgcustom2-5.png'
import upload from '../upload-to-cloud-20.svg'



export default function Plodes( props ) {

    var formState =         props.formState;
    var msg =               props.msg;
    var setInput =          props.setInput;
    var addLogo =           props.addLogo;
    var setFile =           props.setFile;
    var contents =          props.contents;
    var handleTheDropDown = props.handleTheDropDown;
    // var modalShow =         props.modalShow;
    // var setMsg =            props.setMsg;
    // var setModalShow =      props.setModalShow;
    // var setShowyBowie =     props.setShowyBowie;
    

    var { brand } = formState

    return (
        <div
        className="formy"
        key="formyKey">
        <div 
            id="uploadSection" 
            key="upSectKey"
            >

            <a href={ 'https://hpgbrands.com' } >
                <img src={ hpgcust2 } alt="hpg" id="titleimage" />
            </a>

            <br />
            <br />


            <div 
                className="inUploadSection open"
                key="inUpSectKey"
                style={ { 
                    backgroundColor : "transparent" ,
                    padding : '1rem' ,
                    paddingTop : '.5rem' } } >


                <div className="instructions" >
                    <p className="letadd lrgr sm-mar-btm ln-ht">
                        { contents.top01 }
                        <br/>
                        { contents.top02 }
                        {/* Upload Your Customer's Logo. */}
                    </p>
                    <p className="sm-mar-btm">
                        <span className="smll letaddhalf" >
                            { contents.top1 }
                            {/* For best results, please upload a logo limited to three colors, without a background. */}
                            </span>
                    </p>
                </div>
                <br />
                <div>
                    <label
                        id="cloud"
                        htmlFor="ploder"
                        style={ {
                            alignContents : 'center' ,
                            width : '25%' ,
                            marginLeft : 'auto' ,
                            marginRight : 'auto' ,
                            marginBottom : '10px'
                        } }>
                        {/* { svgReturn } */}
                        <img src={ upload } width="70px" height="50px" alt="upldlogo" />
                        <p id="cloudText"
                            className="letadd" >
                            { ( formState.image.length > 1 ) 
                                ? formState.image 
                                : 'browse' }
                        </p>
                    </label>
                </div>


                <input 
                    name="ploder" 
                    id="ploder"
                    type="file"
                    accept="image/*,.pdf,.ai,.eps"
                    multiple
                    className="visually-hidden"
                    onChange={ event => {
                        if ( event.target.files[ 0 ] ) {
                            setInput( 'image' , event.target.files[ 0 ].name )
                            setFile( event )
                    } } } />


                    <Dropdown drop="right"
                        className=" mar-btm-low"
                        onSelect={ handleTheDropDown }
                        >

                        <Dropdown.Toggle
                            id="dropdown-basic"
                            className="ls btn-tranny"
                            >
                            { brand }
                        </Dropdown.Toggle>

                        <Dropdown.Menu>
                            <Dropdown.Item
                                id="ddwebb" 
                                eventKey="webb"
                                >
                                webb
                            </Dropdown.Item>
{/* 
                            <Dropdown.Item
                                disabled
                                id="ddhub"
                                eventKey="hub"
                                >
                                hub
                            </Dropdown.Item>

                            <Dropdown.Item
                                disabled
                                id="ddorigaudio"
                                >
                                origaudio
                            </Dropdown.Item>

                            <Dropdown.Item
                                disabled
                                id="ddbandb"
                                >
                                batch&amp;bodega
                            </Dropdown.Item>

                            <Dropdown.Item
                                disabled
                                id="ddbcg"
                                >
                                bcg
                            </Dropdown.Item>

                            <Dropdown.Item
                                disabled
                                id="ddbeacon"
                                >
                                beacon
                            </Dropdown.Item>

                            <Dropdown.Item
                                disabled
                                id="ddbest"
                                >
                                best
                            </Dropdown.Item>

                            <Dropdown.Item
                                disabled
                                id="dddebco"
                                >
                                debco
                            </Dropdown.Item>

                            <Dropdown.Item
                                disabled
                                id="ddhandstands"
                                >
                                handstands
                            </Dropdown.Item>

                            <Dropdown.Item
                                disabled
                                id="ddpremium"
                                >
                                premium
                            </Dropdown.Item>
                             */}
                        </Dropdown.Menu>
                    </Dropdown>


                {/* <br /> */}

                <input
                    onChange={ event => setInput( 'name' , event.target.value ) }
                    className="input mar-btm-low"
                    value={ formState.name }
                    key="inputNameKey"
                    placeholder="logo name"
                    width="300px" />
                    {/* <br /><br /> */}
                    <br />
                <input
                    onChange={ event => setInput( 'fullname' , event.target.value ) }
                    className="input mar-btm-low"
                    value={ formState.fullname }
                    key="inputYourNameKey"
                    placeholder="your name"
                    width="300px" />
                    <br />
                <input
                    onChange={ event => setInput( 'email' , event.target.value ) }
                    className="input mar-btm-low"
                    type="email"
                    key="inputEmailKey"
                    value={ formState.email }
                    placeholder="email address"
                    required
                    />
                <br />
                <Button className="btn ls btn-tranny" onClick={ addLogo } >submit</ Button>
                <br /><br />
                <p className="smll letadd">{ msg.toLowerCase( ) }</p>
              
            </div>
        </div>

    </div>
    )
}